<template>
  <div class="main">
    <div>
      <van-swipe-cell v-for="(item,index) in list" :key="item.id">
        <van-card :num="item.num" :title="item.goodsname" class="goods-card" :thumb="$pre+item.img">
          <template #tags>
            <!-- 内容 -->
            <div>单价：&yen;{{item.price|filterPrice}}</div>
            <div>总价：&yen;{{item.price*item.num|filterPrice}}</div>
            <!-- <button v-if="item.checked">选中</button>
            <button v-else>没选中</button> -->

            <!-- 选框 -->
            <div class="check" @click="checkeds(index)">
              <img :src="item.checked?img.radio_hig:img.radio_nor" alt />
            </div>
          </template>
          <!-- 加减 -->
          <template #footer>
            <van-button type="info" size="mini" @click="sub(item.id,item.num,index)">-</van-button>
            <van-button type="primary" size="mini" @click="add(item.id,index)">+</van-button>
          </template>
        </van-card>
        <!-- 删除 -->
        <template #right>
          <van-button square text="删除" type="danger" class="delete-button" @click="del(item.id)" />
        </template>
      </van-swipe-cell>
    </div>
 
 <!-- 提交订单 -->
    <van-submit-bar
      class="submit"
      decimal-length="2"
      :price="allPrice"
      button-text="提交订单"
      @submit="onSubmit"
    >
      <div class="allcheck">
        <!-- 全选图标 -->
        <img @click="all" :src="allchecked?img.radio_hig:img.radio_nor" alt />
      </div>
    </van-submit-bar>
  </div>
</template>

<script>
import { reqCartlist, reqCartedit, reqCartdelete } from "../../utils/http";
import { Toast } from "vant";
import radio_nor from "../../assets/img/radio_nor.png";
import radio_hig from "../../assets/img/radio_hig.png";
import { Dialog } from "vant";
export default {
  methods: {
    //获取商品
    getlist() {
      reqCartlist({ uid: JSON.parse(localStorage.getItem("user")).uid }).then(
        res => {
          let arr = res.data.list;
          if (arr == null) {
            this.list = [];
            return;
          } else {
            arr.forEach(item => {
              item.checked = false;
            });
            this.list = arr;
          }
        }
      );
    },
    //添加
    add(id, index) {
      // console.log(id)
      reqCartedit({ id: id, type: 2 }).then(res => {
        if (index == 0 || index) {
          this.list[index].num++;
        }
      });
    },
    // 减少
    sub(id, num, index) {
      if (num == 1) {
        Toast("宝贝不能再少喽");
        return;
      }
      reqCartedit({ id: id, type: 1 }).then(res => {
        if (this.list[index].num == 1) {
          this.add(id);
          this.list[index].num = 1;
          return;
        }

        this.list[index].num--;
      });
    },
    // 删除
    del(id) {
      Dialog.confirm({
        title: "警告",
        message: "是否删除这条数据"
      })
        .then(() => {
          reqCartdelete({ id }).then(res => {
            this.getlist();
          });
        })
        .catch(() => {

        });
    },
    // 提交
    onSubmit() {
      Toast("提交成功");
    },
    //全选
    all() {
      this.allchecked = !this.allchecked;
      this.list.forEach(item => {
        item.checked = this.allchecked;
      });
    },
    //单选
    checkeds(index) {
      this.list[index].checked = !this.list[index].checked;
      this.allchecked = this.list.every(item => item.checked);
    }
  },
  data() {
    return {
      code: 0,
      list: [],
      img: {
        radio_nor,
        radio_hig
      },
      allchecked: false,
      index: ""
    };
  },
  mounted() {
    //获取购物车列表
    this.getlist();
  },
  computed: {
    allPrice() {
      let total = 0;
      this.list.forEach((item, index, arr) => {
        if (item.checked) {
          return (total += item.price * item.num);
        }
      });
      return total * 100;
    }
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';

.delete-button {
  height: 100%;
}

.goods-card {
  position: relative;

  .check {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.submit {
  
}

.van-submit-bar {
  margin-bottom: .8rem
  padding 0
}

.main {
  position: relative;
}
.van-image{
  margin-left .3rem
}
.van-card__content{
  margin-left .3rem
}

</style>


